import React, { useState } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';

const ChatInterface: React.FC = () => {
  const [pdfFile, setPdfFile] = useState<File | null>(null);
  const [pdfChunks, setPdfChunks] = useState<string[]>([]);
  const [question, setQuestion] = useState<string>('');
  const [context, setContext] = useState<string>('');
  const [answer, setAnswer] = useState<string>('');
  const [loading, setLoading] = useState(false);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files[0]) {
      setPdfFile(event.target.files[0]);
    }
  };

  const handleUploadPdf = async () => {
    if (!pdfFile) return;

    const formData = new FormData();
    formData.append('file', pdfFile);

    setLoading(true);
    try {
      const response = await fetch('http://localhost:5000/process-pdf', {
        method: 'POST',
        body: formData,
      });

      const data = await response.json();
      if (response.ok) {
        setPdfChunks(data.chunks);
        setContext(data.chunks.join(' '));
      } else {
        alert(`Error: ${data.error}`);
      }
    } catch (error) {
      alert(`Upload failed: ${error}`);
    } finally {
      setLoading(false);
    }
  };

  const handleAskQuestion = async () => {
    if (!context || !question) {
      alert('Please upload a PDF and ask a question.');
      return;
    }

    setLoading(true);
    try {
      const response = await fetch('http://localhost:5000/ask-question', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ context, question }),
      });

      const data = await response.json();
      if (response.ok) {
        setAnswer(data.answer);
      } else {
        alert(`Error: ${data.error}`);
      }
    } catch (error) {
      alert(`Request failed: ${error}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card className="p-4 max-w-xl mx-auto">
      <CardContent>
        <h2 className="text-xl font-semibold mb-4">Chat Interface</h2>

        <div className="mb-4">
          <Input type="file" onChange={handleFileChange} accept="application/pdf" />
          <Button onClick={handleUploadPdf} className="ml-2" disabled={loading || !pdfFile}>
            {loading ? 'Uploading...' : 'Upload PDF'}
          </Button>
        </div>

        <Textarea
          value={context}
          readOnly
          rows={5}
          className="mb-4"
          placeholder="Extracted context from the PDF will appear here."
        />

        <Input
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          placeholder="Type your question here..."
          className="mb-4"
        />

        <Button onClick={handleAskQuestion} disabled={loading || !context || !question}>
          {loading ? 'Fetching Answer...' : 'Ask Question'}
        </Button>

        {answer && (
          <div className="mt-4 p-2 border rounded bg-gray-50">
            <strong>Answer:</strong> {answer}
          </div>
        )}
      </CardContent>
    </Card>
  );
};

export default ChatInterface;